.location{
  padding-top: $spacer*12;
  padding-bottom: $spacer*16;
  @include pad{
    padding-top: $spacer*7.5;
    padding-bottom: $spacer*4.5;
  }
  @include mobile{
    padding-top: $spacer*3.5;
    padding-bottom: $spacer*5;
  }
  h2{
    @include title;
  }
}

.locationList{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  >li{
    width: 32%;
    margin-bottom: $spacer*4;
    @include pad{
      width: 48%;
      margin-bottom: $spacer*2.5;
    }
    @include mobile{
      width: 100%;
      margin-bottom: $spacer*2;
    }
    &:last-child{
      margin-right: auto;
      margin-left: 2%;
      @include pad{
        margin-right: 0;
        margin-left: 0;
      }
    }
    
  }
}